.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

span.livenum{
  font-size: 48px;
  color : red;
}
span.livenum2{
  color : red;
}
div.messageDiv{
  height : 200px;
  max-height : 200px;
  overflow : scroll;
  text-align : justify;
  padding-left : 10px;
  padding-right : 10px;
  padding-bottom : 10px;
  margin-bottom : 10px;
}
span.username{
  color:blue;
}
span.message{
  color:green;
}
div.marginTop{
  display : absolute;
  bottom : 0;
}
